/* ELEMENTS */

.about {
    margin: 100px 35px;
}

.ingredients .instructions {
    display: block;
    width: 100%;
    margin-top: 25px;
    margin-right: 25px !important;
    padding: 15px 25px;
    font-family: "Quicksand", sans-serif;
    font-size: 17px;
    font-weight: bold;
    background-color: #ffffff;
    color: #000000;
    border-radius: 10px;
}

.recipepage {
    background-image: -webkit-gradient(linear, left top, right top, from(red) , to(yellow));
    background-image: -webkit-linear-gradient(left, red , yellow);
    background-image: -o-linear-gradient(left, red , yellow);
    background-image: linear-gradient(to right, red , yellow);
}

/* BUTTONS */

.set-title {
    display: inline-block;
    padding: 5px 35px 7px 35px !important;
    font-size: 40px !important;
    font-weight: bold;
    border-radius: 50px;
    border: 2px solid #ffffff;
}

.split {
    display: inline-block;
    width: 100%;
    margin-top: 15px;
    padding: 5px 20px;
    font-size: 20px !important;
    font-weight: bold;
    border-radius: 50px;
    text-align: right;
    border: 2px solid transparent;
}

.split:hover {
    background: #ffffff;
    color: #000000;
    border: 2px solid #000000;
}


