/* GENERAL */

.filterholder {
    margin-top: 100px;
}

.filterlist {
    margin-bottom: -100px;
}

/* FILTERS */

.filter {
   margin-right: 10px;
   margin-bottom: 10px;
   padding: 5px 20px !important;
   border-radius: 20px;
   border: 2px solid #ffffff;
}

.filter:hover {
    background-image: -webkit-gradient(linear, left top, right bottom, from(red), to(yellow));
    background-image: -webkit-linear-gradient(top left, red, yellow);
    background-image: -o-linear-gradient(top left, red, yellow);
    background-image: linear-gradient(to bottom right, red, yellow);
    color: #000000;
    border: 2px solid #ffffff;
}



